import gql from 'graphql-tag'

const DELETE_PRODUCT = gql`mutation deleteProduct($id: ID!){
    deleteProduct(id: $id){
        success
    }
}`

const CREATE_PRODUCT = gql`mutation createProduct($product: ProductCreateInput!){
    createProduct(product: $product){
        product{
            id 
            name 
            image 
            sku
        }
    }
}
`
const UPDATE_PRODUCT = gql`mutation updateProduct($product: ProductEditInput!, $id: ID!){
    updateProduct(product: $product, id: $id){
        product{
            updated
            name
            sku
            id
        }
    }
}`

const GET_PRODUCT = gql`query getProduct($sku: String!){
    getProduct(sku: $sku){
        name
        id
        price
        category{
            id
        }
        store{
            id
        }
        active
        stock
    }
}`

export {
    DELETE_PRODUCT,
    CREATE_PRODUCT,
    UPDATE_PRODUCT,
    GET_PRODUCT
}